$primary: #5C62C5;
$dark-grey: #363636;
$ligth-grey: #D8D8D8;

.journal__main-content{
    display: flex;
}

.journal__sidebar{
    background-color: $dark-grey;
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    padding-left: 10px;
    padding-right: 10px;
    width: 450px;
}

.journal__sidebar-navbar{
    display: flex;
    justify-content: space-between;
}

.journal__sidebar-navbar h3{
    font-weight: lighter;

}

.journal__new-entry{
    align-items: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    width: 100%;
    flex-direction: column;

    transition: color .3s ease;

    &:hover{
        color: darken($color: white, $amount: 20);
    }
}

.journal__entries{
    margin-top: 30px;
    flex: 1 1 auto;
    overflow-y: auto;
}

.journal__entry{
    cursor: pointer;
    background-color: white;
    border-radius: 4px;
    color: $dark-grey;
    display: flex;
    margin-bottom: 10px;
    overflow: hidden;
    justify-content: space-between;
}

.journal__entry-picture{
    height: 75px;
    width: 75px;
    flex: 1;
}

.journal__entry-body{
    padding: 10px;
    flex: 1.5;

}
.journal__entry_title{
    font-size: 14px;
    font-weight: bold;
}
.journal__entry-content{
    font-size: 10px;
}

.journal__entry-date-box{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5px;
    flex: 1;
}

.journal__entry-date-box span{
    font-size: 12px;
}

.journal__nothing-main-content{
    align-items: center;
    background-color: $primary;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100vh;

    p{
        text-align: center;
    }
}